<template>
  <div class="modal-overlay" @click.self="$emit('close')">
    <div class="modal">
      <h3>{{ (task && task.id) ? '–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å' : '–ù–æ–≤–∞—è' }} –∑–∞–¥–∞—á–∞</h3>
      <input v-model="localTask.title" placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ" />
      <div class="modal-actions">
        <button @click="$emit('close')">–û—Ç–º–µ–Ω–∞</button>
        <button @click="save">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['task'],
  data() {
    return {
      localTask: { ...this.task } || { title: '' },
    };
  },
  watch: {
    task: {
      immediate: true,
      handler(val) {
        this.localTask = { ...val } || { title: '' };
      },
    },
  },
  methods: {
    save() {
      if (!this.localTask.title.trim()) {
        alert('–ù–∞–∑–≤–∞–Ω–∏–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø—É—Å—Ç—ã–º');
        return;
      }
      this.$emit('save', this.localTask);
    },
  },
};
</script>

